<template>
  <div>

<van-nav-bar title="专栏详情"
                 left-arrow
                 @click-left="onClickLeft" />


   <ul id="specialtwo_comtent">
      <li>
        <p>{{ news.title}}</p> 
        <img alt=""
             :src="news.pic">
        <div v-html="news.content">
          
        </div>
      </li>
    </ul> 
  </div>

</template>

<script>
export default {
  components: {},
  props: {},
  data() {
    return {
        news:{}
    }
  },
  watch: {},
  computed: {},
  methods: {
      onClickLeft(){
          this.$router.go(-1)
      }
  },
  created() {

      var id = this.$route.params.id

      console.log(id)

      this.http.get("/cms/news/detail?id="+id).then((msg)=>{

             console.log(msg)
             if(msg.data.msg =="success"){
                this.news = msg.data.data
             }
             
      })
  },
  mounted() {}
}
</script>
<style lang="less">
.specialtwo_top {
  width: 100%;
  height: 0.7rem;
  background: #ffffff;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 0.3rem;
  position: fixed;
  left: 0;
  top: 0;
  > p:nth-of-type(1) {
    font-size: 0.24rem;
  }
  > p:nth-of-type(2) {
    font-size: 0.2rem;
  }
}
#specialtwo_comtent {

  > li {
    > p:nth-of-type(1) {
      font-size: 0.3rem;
      color: #333333;
      width: 100%;
      text-align: center;
      margin-top: 0.45rem;
      margin-bottom: 0.2rem;
    }
    > img {
      width: 4.85rem;
      height: 3.13rem;
      display: block;
      margin: 0 auto;
    }
    > div:nth-of-type(1) {
      padding: 0 0.15rem;
      p {
        font-size: 0.2rem;
        color: #333333;
        line-height: 0.48rem;
        text-indent: 2em;
        margin-top: 0.1rem;
      }
    }
  }
}
</style>


