import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Home from '@/pages/Home'

import Index from '@/pages/Index'
import Cate from '@/pages/Cate'
import Cart from '@/pages/Cart'
import User from '@/pages/User'
import List from '@/pages/List'
import Detail from '@/pages/Detail'
import News_detail from '@/pages/News_detail'
import News_detail2 from '@/pages/News_detail2'
import Zllist from "@/pages/Zl_list"

import Zhuce  from "@/pages/Zhuce"
import Login  from "@/pages/Login"
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld,
      redirect:"/home"
    },
    {
        path:"/home",
        name:"home",
        component:Home,
        redirect:"/home/index",
        children:[
               {
                  path:"index",
                  component:Index 
               },
               {
                path:"cate",
                component:Cate,
                meta:{
                    keepAlive:true
                }
             },
             {
                path:"cart",
                component:Cart 
             },
             {
                path:"user",
                component:User 
             }

        ]
    },
    {
        path:"/list",
        component:List
    },
    {
        path:"/detail",
        component:Detail,
        meta:{
            keepAlive:true
        }
    },
    {
        path:"/news_detail",
        component:News_detail
    },
    {
        path:"/news_detail2/:id",
        component:News_detail2
    },
    {
        path:"/zllist",
        component:Zllist
    },
    {
        path:"/zhuce",
        component:Zhuce
    },
    {
        path:"/login",
        component:Login
    }

  ]
})



