<template>
  <div class="wrapper">
        <input type="text">
        <ul>
            <li v-for="item in list" >
                {{ item.name }}
            </li>
        </ul>
        {{ msg }}
        <input type="button" value="xiugai" @click="f1" />
  </div>
</template>

<script>
export default {
  components:{},
  props:{},
  data(){
    return {
        msg:"1111111111",
        list:[{name:"react"},{name:"vue"}]
    }
  },
  watch:{},
  computed:{},
  methods:{
      f1(){

         // this.msg = "1111111111"
         var arr = [{name:"1111"},{name:"2222"}]
         //arr[1].name = "123123"

         this.list = arr
      }
  },
  created(){},
  mounted(){},
  updated(){
       console.log("xiugai 123")
  },
  beforeRouteEnter(from,to,next){

        //  next((vm)=>{
             
        //        vm.msg = "router"

        //  })
        //this.msg = "3333333333"
        next()
  },
  activated(){
         console.log("alive进入11111111111")
  },
  deactivated(){
         console.log("alive 离开")

  }
}
</script>
<style scoped>
.wrapper{}
</style>


