<template>
  <div class="login">
        
        <p>
           手机号 <input type="text" v-model="phone" />
        </p>
          <p>
            密码 <input type="password" v-model="password" />
        </p>
        <input type="button" value="登录" @click="login" />
     
  </div>
</template>

<script>
export default {
  components:{},
  props:{},
  data(){
    return {
        phone:"",
        password:""
    }
  },
  watch:{},
  computed:{},
  methods:{
      login(){

          this.http({
              url:"https://api.it120.cc/small4/user/m/login?deviceId=007&deviceName=monkey",
              method:"post",
              params:{
                  mobile:this.phone,
                  pwd:this.password
              }
          }).then((msg)=>{
                
                console.log(msg)
                if(msg.data.msg == "success"){
                    localStorage.setItem("token",msg.data.data.token);
                    this.$router.push("/home/user")

                }
                



          })
      }
  },
  created(){},
  mounted(){}
}
</script>
<style scoped>
.wrapper{}
</style>


