<template>
  <div class="zhuce">
    <van-cell-group>
        <van-field
        v-model="phone"
        required
        label="手机号"
        placeholder="请输入手机号"
        error-message="手机号格式错误"
    />
    <van-field v-model="password" type="password"  label="密码" />
    <van-field v-model="password2" type="password" label="密码" />
    <van-field  label="图像验证码" v-model="img" />  
    <img :src="pic" alt="" @click="ch" >
    <van-field
    v-model="sms"
    center
    clearable
    label="短信验证码"
    placeholder="请输入短信验证码"
    >
    <template #button>
        <van-button size="small" type="primary" @click="sendmsg" >发送验证码</van-button>
    </template>
    </van-field>


    </van-cell-group>
     
     <van-button @click="zhuce">
         注册
     </van-button>
  </div>
</template>

<script>

const { v4: uuidv4 } = require('uuid');
var k = uuidv4();   // 生成唯一的字符串
export default {
  components:{},
  props:{},
  data(){
    return {
        pic:"https://api.it120.cc/small4/verification/pic/get?key=" + k,
        phone:"",
        password:"",
        password2:"",
        key:k,
        sms:"",
        img:""
    }
  },
  watch:{},
  computed:{},
  methods:{
      ch(){
          // 图形验证码切换  

           var key = uuidv4();

           this.key = key
           this.pic = "https://api.it120.cc/small4/verification/pic/get?key=" + key
      },
      sendmsg(){
        // 获取短信验证码
          this.http({
                url:"https://api.it120.cc/small4/verification/sms/get",
                method:"post",
                params:{
                    mobile:this.phone,   // 手机号
                    key:this.key,         // 图形验证码对应的key
                    picCode:this.img      // 图形验证码的字符
                }
          }).then((msg)=>{
              
              console.log(msg)

          })
      },
      zhuce(){
          // alert(123)  
           console.log(this.phone)
           // 把注册需要的属性  提交到服务器 完成注册
          this.http({
              url:"https://api.it120.cc/small4/user/m/register",
              method:"post",
              params:{
                  mobile:this.phone,
                  pwd:this.password,
                  code:this.sms
              }
          }).then((msg)=>{

               console.log(msg)
               if(msg.data.msg == "success"){

                  // alert("")
                  this.$router.push("/login")
               }

          })

      }
  },
  created(){},
  mounted(){}
}
</script>
<style scoped>
.zhuce{}
</style>


