<template>
  <div class="list">
      <van-nav-bar
        title="商品列表"
        left-arrow
        @click-left="onClickLeft"
        />
        <van-loading type="spinner" color="#1989fa" v-if="loading" />
        <div v-else>
            <div class="k" v-show="list.length==0">
                <img src="http://39.105.231.166:7002//static/img/fc9016c81219eb449d20df78381e70af.4b93135.png" alt="">
            </div>
            <ul class="goods">
                <li v-for="item in list" :key="item.id">
                    <img :src="item.pic" alt="">
                    <p>{{ item.name }}</p>
                    <p>￥{{ item.originalPrice}}</p>
                </li>
            </ul>
        </div>
  </div>
</template>

<script>
export default {
  components:{},
  props:{},
  data(){
    return {
        list:[],
        loading:true
    }
  },
  watch:{},
  computed:{},
  methods:{
      onClickLeft(){
          this.$router.go(-1)
      }
  },
  created(){
      
      //console.log(this.$route.query.cateid)
      this.http.get("https://api.it120.cc/small4/shop/goods/list").then((msg)=>{

                //console.log(msg)

               this.list  = msg.data.data.filter((item)=>{
                      
                    return  item.categoryId == this.$route.query.cateid

                })
                this.loading = false

      })
  },
  mounted(){}
}
</script>
<style scoped lang="less">

.goods{
    margin-top: 0.7rem;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    padding: 0 0.1rem;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    li{
        width: 2.45rem;
       position: relative;
       img{
               width: 100%;
       }
       p:nth-of-type(1){
position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 0.4rem;
    background: #F0EDD4;
    font-size: 0.14rem;
    color: #B69B72;
    text-indent: 0.02rem;
    line-height: 0.4rem;
       }
       p:nth-of-type(2){
           font-size: 0.2rem;
    color: #CF1311;
    position: absolute;
    left: 0;
    top: 2.5rem;
       }
    }

}

.list{
    .k{
        img{
                width: 100%;
                margin-top: 2rem;
        }
    }
}
</style>


