<template>
  <div class="cate">
        <div class="search">
              <input type="text" :placeholder="msg" />   
        </div>
        <p> {{ msg }}</p>
        <div class="fl">
            <ul>
                <li :class="{'active':n==0}" @click="tab(0)" >全部分类</li>
                <li v-for="(item,index) of list1" :class="{'active':n==index+1}" @click="tab(index+1,item.id)" :key="item.id">
                        {{ item.name }}

                </li>
            </ul>
            <div>
                <ul>
                    <li v-for="item in fl2" :key="item.id" @click="golist(item.id)" >
                        <img :src="item.icon" alt="">
                         <p>{{ item.name }}</p>
                    </li>
                </ul>

            </div>
        </div>



  </div>
</template>

<script>
export default {
  components:{},
  props:{},
  data(){
    return {
        list:[],
        n:0,
        fl2:[],
        msg:"111111111111"

    }
  },
  watch:{},
  computed:{
        list1(){
            return this.list.filter((item)=>{

                  return item.level == 1
            })

        }

  },
  methods:{
      tab(index,id){
           this.n = index
           if(index ==0){

                this.fl2 = this.list
           }else{
                var fl2 = this.list.filter((item)=>{
                        return item.pid == id
                })
                this.fl2 = fl2

           }
      },
      golist(id){

                this.$router.push("/list?cateid="+id)
      }

  },
  created(){
      console.log("created 111111111111")
      this.http.get("https://api.it120.cc/small4/shop/goods/category/all").then((msg)=>{

        // console.log(msg)
        this.list = msg.data.data
        this.fl2 = msg.data.data

      })
  },
  mounted(){

        console.log("挂载完全")

        this.msg = "22222222222"
  },
  destroyed(){
        
        console.log("组件销毁")
  },
  activated(){
        
        console.log("从内存中载入")
  },
  deactivated(){

       console.log("从内存中离开")
  },
  beforeRouteEnter(from,to,next){
         
         console.log("路由进入前");
         // this  这里的this  指向不是当前组件
         next((vm)=>{
                
               vm.msg = "333333333333333"

         })
         
      
  }
}
</script>
<style scoped lang="less">
.cate{
     .search{
         
            width: 100%;
            height: 0.5rem;
            border-bottom: 0.01rem solid #F3F3F3;
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            -webkit-box-align: center;
            -ms-flex-align: center;
            align-items: center;
            input{
                width: 3.47rem;
                height: 0.35rem;
                background: #F5F5F5;
                border: none;
                border-radius: 0.2rem;
                margin-left: 0.2rem;
                padding-left: 0.2rem;
            }   
   }
   .fl{
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
       >ul{
            width: 1.27rem;
            border-right: 0.01rem solid #E9E9E9;
            height: 100%;
            position: fixed;
            left: 0;
            top: 0.51rem;
            overflow: auto;
            background: white;
            z-index: 1;
            li{
                width: 1.27rem;
                height: 0.5rem;
                font-size: 0.2rem;
                text-align: center;
                line-height: 0.5rem;
                margin-top: 0.1rem;
            }
            li.active{
                border-left:red 1px solid;
                color:red
            }
       }
       >div{
            position: fixed;
            left: 1.27rem;
            top: 0.51rem;
            overflow: auto;
            height: 100%;
           ul{
                display: -webkit-box;
                display: -ms-flexbox;
                display: flex;
                -webkit-box-pack: justify;
                -ms-flex-pack: justify;
                justify-content: space-between;
                -ms-flex-wrap: wrap;
                flex-wrap: wrap;
                width: 3.72rem;
                text-align: center;
                padding: 0 0.1rem;
                li{
                    width: 30%;
                    margin-top: 0.1rem;
                    img{
                        width: 1.1rem;
                        height: 1.1rem;
                        display: block;
                    }
                    p{
                        font-size: 0.16rem;
                        color: #3E4561;
                        margin-top: 0.05rem;
                    }
                }

           }
       }

   }

      


}
</style>


