<template>
  <div class="zl">
    <van-nav-bar title="严选专栏"
                 left-arrow
                 @click-left="onClickLeft" />
    <van-loading type="spinner" size="24px" v-if="loading" />             
    <ul id="special_content" v-for="item of list" :key="item.id"  v-else >
      <li class="" @click="godetail(item.id)" >
          <img :src="item.pic"
             alt="">
        <p>{{ item.title}}</p>
        <p>{{ item.descript}}</p>
        <p>查看详情</p>
      </li>

    </ul>

  </div>
</template>

<script>
export default {
  components: {},
  props: {},
  data() {
    return {
        list:[],
        loading:true
    }
  },
  watch: {},
  computed: {},
  methods: {
    onClickLeft() {
      this.$router.go(-1)
    },
    godetail(id){
          
          this.$router.push("/news_detail2/" + id )

    }
  },
  created() {
    this.http.get('/cms/news/list').then(msg => {
      console.log(msg)
      this.loading = false;
      this.list = msg.data.data
    })
  },
  mounted() {}
}
</script>
<style scoped>
#special_content li {
    padding-left: 0.09rem;
    padding-top: 0.09rem;
    padding-right: 0.09rem;
    text-align: center;
    position: relative;
}
#special_content li img {
    width: 5.02rem;
    height: 2.56rem;
    display: block;
    background: rgba(0,0,0,0.7);
}
#special_content li p:nth-of-type(1) {
    position: absolute;
    top: 0.6rem;
    left: 10%;
    width: 80%;
    text-align: center;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-size: 0.26rem;
    color: white;
}
#special_content li p:nth-of-type(2) {
    position: absolute;
    top: 1.2rem;
    left: 10%;
    width: 80%;
    text-align: center;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-size: 0.2rem;
    color: white;
}
#special_content li p:nth-of-type(3) {
    position: absolute;
    top: 1.64rem;
    left: 1.94rem;
    width: 1.08rem;
    height: 0.38rem;
    text-align: center;
    line-height: 0.38rem;
    font-size: 0.18rem;
    color: white;
    border: 0.01rem solid #FFFFFF;
    border-radius: 0.5rem;
}
</style>


